$primary-font: 'Roboto Condensed', sans-serif;
$secondary-font: 'Lato', sans-serif;
$manuscrit-font: 'Oooh Baby', cursive;

/* Spacing
-------------------------------*/
$space: 20px;
$space-double: 40px;

/* Breakpoints
-------------------------------*/
$tablet: "(min-width: 450px)";
$max-desktop: "(max-width: 768px)";
$mid-point: "(min-width: 620px)";
$desktop: "(min-width: 768px)";


/* Breakpoints
-------------------------------*/
$screen-xs: 480px;
$screen-sm: 768px;
$screen-md: 992px;
$screen-lg: 1200px;

$bpoints: (
        'xsmall' : (max-width: $screen-xs),
        'lsmall' : (min-width: $screen-xs),
        'small' : (min-width: $screen-sm),
        'medium' : (min-width: $screen-md),
        'large' : (min-width: $screen-lg)
);
